  z-index: 10;
  top: 0;
  left: 5rem;
  width: 6rem;
  border: 2px solid var(--color-orange);
}

.img_socials {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 5px;
}

.img_socials:hover {
  /* background-color: var(--color-orange); */
  opacity: 0.75;
}

@media screen and (max-width: 1024px) {
  .footerLink {
    flex-direction: column;
    align-items: flex-start;
