/* ------------------------- Product Galery ------------------------- */
#productGallery {
  margin-top: 10rem;
  display: grid;
  grid-template-columns: 49% 49%;
  grid-template-rows: 17rem 17rem;
  grid-gap: 1.5rem;
}

.pgImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.pgItem1 {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.pgItem2 {
  grid-column: 1 / 2;
  grid-row: 2 / 2;
}

.pgItem3 {
  grid-column: 2 / 2;
  grid-row: 1 / 3;
  /* height: 35.5rem; */
}

@media screen and (max-width: 900px) {
  #productGallery {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .pgImg {
    width: 60%;
    object-fit: cover;
    display: block;
  }
}

